.Aside {
  grid-row: 2 / -1;
  grid-column: 1 /2;
  background-color: #fafaff;

  position: fixed;
  // overflow-y: scroll;
  top: 5rem;
  left: 0;
  bottom: 0;
  width: 15vw;
  z-index: 149;

  &__Nav {
    list-style: none;
    margin: 2rem 0 2rem 1vw;
    &_item {
      font-size: 0.8rem;

      &:not(:last-child) {
        margin-bottom: 0.1rem;
      }

      &-link {
        display: grid;
        align-items: center;
        color: rgba(#4c4d50, 0.7);
        font-family: inherit;
        font-weight: 500;
        text-decoration: none;
        transition: all ease-out 0.3s;
        padding: 1rem 1rem 1rem 2rem;
        grid-template-columns: 15% 85%;

        &:hover:not(.active) {
          color: #4c4d50;
          transform: translateX(8px);
        }
      }

      &-children {
        display: flex;
        align-items: center;
        color: rgba(#4c4d50, 0.7);
        font-family: inherit;
        font-weight: 500;
        text-decoration: none;
        transition: all ease-out 0.3s;
        font-size: 0.6rem;
        margin-left: 2rem;
        padding: 0.5rem 1rem 0.5rem 2rem;

        &-children {
          display: flex;
          align-items: center;
          color: rgba(#4c4d50, 0.7);
          font-family: inherit;
          font-weight: 500;
          text-decoration: none;
          transition: all ease-out 0.3s;
          font-size: 0.6rem;
          margin-left: 3rem;
          padding: 0.5rem 1rem 0.5rem 2rem;

          &:hover:not(.active) {
            color: #4c4d50;
            transform: translateX(8px);
          }
        }

        &:hover:not(.active) {
          color: #4c4d50;
          transform: translateX(8px);
        }
      }

      &-disabled {
        display: grid;
        align-items: center;
        color: rgba(#4c4d50, 0.7);
        font-family: inherit;
        font-weight: 500;
        text-decoration: none;
        transition: all ease-out 0.3s;
        padding: 1rem 1rem 1rem 2rem;
        grid-template-columns: 15% 80% 5%;

        &-children {
          display: grid;
          align-items: center;
          color: rgba(#4c4d50, 0.7);
          font-family: inherit;
          font-weight: 500;
          text-decoration: none;
          font-size: 0.6rem;
          transition: all ease-out 0.3s;

          margin-left: 2rem;
          padding: 0.5rem 1rem 0.5rem 2rem;
          grid-template-columns: 15% 80% 5%;

          &:hover {
            color: #4c4d50;
            transform: translateX(8px);
          }
        }

        &:hover {
          color: #4c4d50;
          transform: translateX(8px);
        }
      }

      &-icon {
        margin-right: 1rem;
      }
    }
  }
}

.Aside__Nav_item-link.active, .Aside__Nav_item-children.active, .Aside__Nav_item-children-children.active {
  background-color: #ed1c24;
  border-top-left-radius: 25px;
  border-bottom-left-radius: 25px;
  color: var(--color-white);
}

@media only screen and (max-width: 899px) {
  .Aside {
    z-index: 80;
    width: 19rem;
    top: 4rem;

    &:before {
      background-color: rgba(0,0,0,0.7);
    }
  }
}

.whatshap {
  font-size: 35px;
  color: rgb(65, 199, 65);
  margin-right: 5rem;
}

.wa {
  margin: 2rem 0 2rem 2.5vw;
}